import React, { Component } from 'react';
import './App.scss';
import 'react-dropdown/style.css'
import SettingsMenu from './components/SettingsMenu';
import {ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend} from 'recharts';

let colors = ['#e6194b', '#3cb44b', '#ffe119', '#4363d8', '#f58231', '#911eb4', '#46f0f0', '#f032e6', '#bcf60c', '#fabebe', '#008080', '#e6beff', '#9a6324', '#fffac8', '#800000', '#aaffc3', '#808000', '#ffd8b1', '#000075', '#808080', '#000000']

let color_index = 0;
let new_curve = [];
let maxRender = 20; //Max number of curves to render

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      graphData: [],
      curves: []
    }
  }

  newDataPoint = (obj, curveCount) => {
    if (curveCount > maxRender) {
      for (let i = 0; i < curveCount - maxRender; i++) {
        delete obj['y'+i];
      }
      new_curve.push(obj);
    } else {
      new_curve.push(obj);
    }
  }

  updateGraph = (curveCount) => {
    console.log("UPDATE", new_curve);
    let curves = [];
    if (curveCount > maxRender) {
      for (let i = curveCount - maxRender - 1; i <= curveCount;i++) {
        curves.push(i); 
      }
    } else {
      for (let i = 0; i <= curveCount;i++) {
        curves.push(i); 
      }
    }
    this.setState({graphData: new_curve,curves: curves});
    new_curve = [];
    console.log("Graph update data:", this.state.graphData);
  }

  render() {
    return (
    <div>
      <SettingsMenu updateGraphMethod={this.updateGraph} addNewData={this.newDataPoint}/>
      <div className="main-view">
        <div className="middle-view" style={{height: '95vh', width: '80%'}}>
          <ResponsiveContainer width='95%' height='100%'>
            <LineChart data={this.state.graphData}
                  margin={{top: 5, right: 30, left: 20, bottom: 5}}>
             <XAxis interval={19} dataKey="x"/>
             <YAxis/>
             <CartesianGrid strokeDasharray="3 3"/>
             <Tooltip/>
             <Legend />
              {
                this.state.curves.map((val,i) => {
                  if (val > colors.length) {
                    color_index = val - (Math.floor(val / colors.length)*colors.length);
                  } else {
                    color_index = val 
                  }
                  return (<Line dataKey={`y${val} ADC`} stroke={colors[color_index]} key={i} />)
                })
              }
            </LineChart>
          </ResponsiveContainer>
        </div>
      </div>

    </div>
    );
  }
}

export default App;
